 <template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form :model="form" ref="ruleForm" label-width="180px" class="demo-ruleForm">
        <el-form-item label="活动名称：" required>
          <el-input style="width:400px;" v-model.trim="form.name" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="活动商品：" required>
          <div v-if="commodityList.length > 0">
            <!-- 统一商品规格 -->
            <div v-if="commodityList[0].skuType == 0">
              <el-table :data="commodityList" tooltip-effect="dark" style="width: 100%">
                <el-table-column label="商品名称" prop="productName"></el-table-column>
                <el-table-column label="售价（元）" prop="price" width="140">
                  <template slot-scope="scope">{{ scope.row.list[0].price ? '￥'+scope.row.list[0].price : '-' }}</template>
                </el-table-column>
                <el-table-column label="成本价（元）" width="140">
                  <template slot-scope="scope">{{ scope.row.list[0].costPrice ? '￥'+scope.row.list[0].costPrice : '-' }}</template>
                </el-table-column>
                <el-table-column width="180">
                  <template slot="header" slot-scope="scope">
                    <span :data-scope="scope">砍价底价（元）</span>
                    <el-tooltip class="item" effect="dark" content="售价 > 底价 >= 0" placement="top">
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                  </template>
                  <template slot-scope="scope">
                    ￥<el-input-number :disabled="form.activityStatus == 1" style="width:100px;" v-model="scope.row.list[0].finallyPrice" :precision="2" :controls="false" :min="0"></el-input-number>
                  </template>
                </el-table-column>
                <el-table-column label="库存" prop="skuStock" width="140">
                  <template slot-scope="scope">{{ scope.row.list[0].skuStock}}</template>
                </el-table-column>
                <el-table-column width="140">
                  <template slot="header" slot-scope="scope">
                    <span :data-scope="scope.row">活动库存</span>
                    <el-tooltip class="item" effect="dark" content="用户支付成功扣除活动库存，退货不增加活动库存" placement="top-start">
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                  </template>
                  <template slot-scope="scope">{{scope.row.list[0].lockStock}}</template>
                </el-table-column>
                <el-table-column label="增加活动库存" width="140">
                  <template slot-scope="scope">
                    <el-input-number style="width:100px;" v-model="scope.row.list[0].addLockStock" step-strictly :controls="false" :min="0"></el-input-number>
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <!-- 多规格商品 -->
            <div v-if="commodityList[0].skuType == 1">
              <el-card shadow="always">
                <div class="flex-row-start">
                  <div>{{commodityList[0].productName}}</div>
                </div>
                <el-table :data="commodityList[0].list" tooltip-effect="dark" style="width: 100%">
                  <el-table-column label="规格" prop="skuName"></el-table-column>
                  <el-table-column label="售价（元）" prop="price" width="140">
                    <template slot-scope="scope">{{ scope.row.price ? '￥'+scope.row.price : '-' }}</template>
                  </el-table-column>
                  <el-table-column label="成本价（元）" width="140">
                    <template slot-scope="scope">{{ scope.row.costPrice ? '￥'+scope.row.costPrice : '-' }}</template>
                  </el-table-column>
                  <el-table-column width="180">
                    <template slot="header" slot-scope="scope">
                      <span :data-scope="scope">砍价底价（元）</span>
                      <el-tooltip class="item" effect="dark" content="售价 > 底价 >= 0" placement="top">
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                    </template>
                    <template slot-scope="scope">
                      ￥<el-input-number :disabled="form.activityStatus == 1" @input="change($event)" style="width:100px;" v-model="scope.row.finallyPrice" :controls="false" :precision="2" :min="0"></el-input-number>
                    </template>
                  </el-table-column>
                  <el-table-column label="库存" prop="skuStock" width="140"></el-table-column>
                  <el-table-column width="140" prop="lockStock">
                    <template slot="header" slot-scope="scope">
                      <span :data-scope="scope.row">活动库存</span>
                      <el-tooltip class="item" effect="dark" content="用户支付成功扣除活动库存，退货不增加活动库存" placement="top-start">
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                    </template>
                  </el-table-column>
                  <el-table-column label="增加活动库存" width="140">
                    <template slot-scope="scope">
                      <el-input-number @input="change($event)" style="width:100px;" v-model="scope.row.addLockStock" step-strictly :controls="false" :min="0"></el-input-number>
                    </template>
                  </el-table-column>
                </el-table>
                <div class="flex-row-start" style="margin-top:20px">
                  <el-popover placement="bottom" trigger="click" v-model="discountvisible">
                    <div class="flex-row-start" style="margin-left:20px;">
                      <el-input-number style="width:100px;" v-model="alldiscount" :controls="false" :min="1"></el-input-number>
                      <el-button type="text" style="margin:0 20px;" @click="allChangediscount">确定</el-button>
                    </div>
                    <el-button slot="reference" :disabled="form.activityStatus == 1">砍价底价</el-button>
                  </el-popover>
                  <el-popover style="margin-left:40px;" placement="bottom" trigger="click" v-model="stockvisible">
                    <div class="flex-row-start" style="margin-left:20px;">
                      <el-input-number style="width:100px;" v-model="allstock" step-strictly :controls="false" :min="0"></el-input-number>
                      <el-button type="text" style="margin:0 20px;" @click="allChangestock">确定</el-button>
                    </div>
                    <el-button slot="reference">增加库存</el-button>
                  </el-popover>
                </div>
              </el-card>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="帮砍金额：" required>
          <el-radio-group :disabled="form.activityStatus == 1" v-model="form.calculateType">
            <el-radio :label="1">随机金额</el-radio>
            <el-radio :label="2">固定金额</el-radio>
          </el-radio-group>
          <div class="infoFont">设置每位帮砍用户的砍价金额规格。</div>
        </el-form-item>
        <el-form-item label="帮砍人数：" required>
          <el-input-number :disabled="form.activityStatus == 1" v-model="form.helpCount" step-strictly :controls="false" :min="2" :max="100" placeholder="2~100"></el-input-number>
          <div class="infoFont">2~100.每个砍价订单的帮砍人数，达到该人数才可砍至底价</div>
        </el-form-item>
        <el-form-item label="展示人数：">
          <el-radio-group :disabled="form.activityStatus == 1" v-model="form.isShow">
            <el-radio :label="0">展示</el-radio>
            <el-radio :label="1">不展示</el-radio>
          </el-radio-group>
          <el-button type="text" @click="showImg1 = true">查看示例</el-button>
        </el-form-item>
        <el-form-item label="活动开始时间：" required>
          <el-date-picker
            :disabled="form.activityStatus == 1"
            v-model="form.beginTime"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择开始时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="活动结束时间：" required>
          <el-date-picker
            v-model="form.endTime"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择结束时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="活动有效期：">
          <el-input-number :disabled="form.activityStatus == 1" v-model="form.activityValidity" step-strictly :controls="false" :min="1" :max="48" placeholder="1~48"></el-input-number> 小时
          <div class="infoFont">1~48.用户发起砍价到砍价截止的时间，不设置则按照活动结束时间进行限制。</div>
        </el-form-item>
        <el-form-item label="限购数量：" required>
          <el-input-number :disabled="form.activityStatus == 1" v-model="form.limitNumber" step-strictly :controls="false" :min="0"></el-input-number>
          <div class="infoFont">限制每个用户发起砍价的次数，输入0代表不限制发起砍价次数。</div>
        </el-form-item>
        <el-form-item label="分享朋友圈标题：">
          <el-input v-model.trim="form.wxMomentsTitle" style="width:400px;" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="分享好友标题：">
          <el-input v-model.trim="form.wxFriendsTitle" style="width:400px;" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="分享好友文案：">
          <el-input v-model.trim="form.wxFriendsContent" style="width:400px;" maxlength="30" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="分享示例：">
          <el-button type="primary" @click="showImg = true">查看示例</el-button>
        </el-form-item>
        <el-form-item label="重要提醒：" required>
          <div class="dangerFont">砍价活动与所有活动互斥</div>
        </el-form-item>
      </el-form>
    </el-card>
    <div class="flex-row-center" style="margin-top:20px;">
      <el-button @click="back">返 回</el-button>
      <el-button :loading="subLoading" type="primary" @click="subForm">保 存</el-button>
    </div>
    <!-- 砍价示例 -->
    <el-dialog title="示例" :visible.sync="showImg" width="30%" center>
      <div class="flex-row-center">
        <img style="max-width:100%;" src="../../../assets/images/marketManage/share1.png" alt="">
      </div>
    </el-dialog>
    <!-- 展示人数示例 -->
    <el-dialog title="示例" :visible.sync="showImg1" width="30%" center>
      <div class="flex-row-center">
        <img style="max-width:100%;" src="../../../assets/images/marketManage/share3.png" alt="">
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { updateShopActivity , getShopActivityById } from '@/api/marketManage/discount'
export default {
  data(){
    return{
      form:{
        calculateType:1,
        isShow:0,
        time:[]
      },
      subLoading:false,
      commodityList:[],
      showImg:false,      // 示例
      showImg1:false,     // 展示人数示例
      // 商品弹出框
      commodityIds:[],
      commodityShow:false,
      alldiscount:undefined,
      allstock:undefined,
      discountvisible:false,
      stockvisible:false,
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getShopActivityById({id:this.$route.query.id}).then(res=>{
        console.log('活动详情',res)
        if(res.code == 200){
          this.form = res.data;
          this.form.time = [res.data.beginActivityTime,res.data.endActivityTime]
          this.commodityList = res.data.productList
        }else{
          this.$message.error(res.message);
        }
      })
    },
    allChangediscount(){
      console.log('this.commodityList',this.commodityList)
      this.commodityList[0].list.forEach(e => {
        e.finallyPrice = this.alldiscount
      })
      this.alldiscount = undefined
      this.discountvisible = false
      this.change()
    },
    allChangestock(){
      this.commodityList[0].list.forEach(e => {
        e.addLockStock = this.allstock
      })
      console.log(this.commodityList)
      this.allstock = undefined
      this.stockvisible = false
      this.change()
    },
    subForm(){
      console.log('this.form',this.form)
      console.log('this.commodityList',this.commodityList)
      if(!this.form.name){
        this.$message.error('请输入活动名称');
        return;
      }
      if(this.commodityList.length == 0){
        this.$message.error('请添加参与活动的商品');
        return;
      }
      let errArr = this.commodityList[0].list.filter(e => e.finallyPrice == undefined || e.finallyPrice >= e.price)
      console.log('errArr',errArr)
      if(errArr.length > 0){
        this.$message.error('商品列表中存在不完整或不正确的数据(砍价底价应小于售价，活动库存应小于商品库存)');
        return;
      }
      let errArr1 = this.commodityList[0].list.filter(e => e.addLockStock > e.skuStock)
      console.log('errArr1',errArr1)
      if(errArr1.length > 0){
        this.$message.error('商品列表中新增活动库存需要小于等于商品库存');
        return;
      }
      this.form.productList = this.commodityList
      if(!this.form.helpCount){
        this.$message.error('请输入帮砍人数');
        return;
      }
      if(!this.form.beginTime){
        this.$message.error('请设置活动开始时间');
        return;
      }
      if(!this.form.endTime){
        this.$message.error('请设置活动结束时间');
        return;
      }
      let dateBegin = new Date(this.form.beginTime.replace(/-/g, "/")).getTime() ;
      let dateEnd = new Date(this.form.endTime.replace(/-/g, "/")).getTime() ;
      if(dateEnd <= dateBegin){
        this.$message.error('结束时间应大于开始时间');
        return;
      }
      if(this.form.limitNumber == undefined){
        this.$message.error('请输入活动限购数');
        return;
      }
      this.subLoading = true;
      this.form.shopId = this.shop.shopId
      this.form.activityType = 2
      this.form.userId = this.user.userId
      updateShopActivity(this.form).then(res=>{
        console.log('修改活动',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          setTimeout(()=>{
            this.subLoading = false;
            this.$router.replace({
              path:'/marketManage/bargain'
            })
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    back(){
      this.$confirm('确定放弃编辑?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({
          path:'/marketManage/bargain'
        })
      }).catch(() => {});
    },
    changeItem(){
      
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .pageContent /deep/ .el-range-separator{
    line-height: 24px;
  }
</style>